import plus from './images/icon-plus.svg';
import minus from './images/icon-minus.svg';
import forward from './images/icon-reply.svg';
import deletes from './images/icon-delete.svg';
import Modal from './Modals';
import { useState } from 'react';
import Comment from './Comment';

function Replies(props) {
  const [data, setData] = useState(props.item);
  const [replyStates, setReplyStates] = useState(data.map(() => false));
  const [show, setShow] = useState(false);
  const [reply, setReply] = useState(false);
  const handleClose = () => setShow(false);
  const handleShow = () => setShow(true);
  const [score, setScore] = useState(data.map((item) => item.score));

  const item = data.map((items, index) => 
    (
    <div key={items.id} className='main1'>
      <div className='arithmetic'>
        <img
          className='icon'
          src={plus}
          alt='plus'
          onClick={() =>
            setScore((prevScore) => ({
              ...prevScore,
              [items.id - 3]: prevScore[items.id - 3] + 1,
            }))
          }
        />
        <br /> <br />
        <h4>
          <span className='blue'>{score[items.id - 3]}</span>
        </h4>
        <img
          className='icon'
          src={minus}
          alt='minus'
          onClick={() =>
            setScore((prevScore) => ({
              ...prevScore,
              [items.id - 3]: prevScore[items.id - 3] - 1,
            }))
          }
        />
      </div>
      <div className='inner'>
        <div>
          <div className='one'>
            <div>
              <span className='in'>
                <img className='profile' src={require(`.${items.user.image.png}`)} alt='profile pic' />
                <p>{items.user.username}</p>
              </span>
            </div>
            <div>
              {items.id === 3 ? (
                <span className='in' onClick={() => setReply(!reply)}>
                  <img className='icon' src={forward} alt='reply' />
                  <p>
                    <span className='blue'>Reply</span>
                  </p>
                </span>
              ) : (
                <div className='aw'>
                  <span className='in' onClick={handleShow}>
                    <img className='icon delete' src={deletes} alt='reply' />
                    <p>
                      <span className='blue'>delete</span>
                    </p>
                  </span>
                  <span
                    className='in'
                    id='reply'
                    
                  >
                    <img className='icon delete' src={forward} alt='reply' />
                    <p>
                      <span className='blue'>Reply</span>
                    </p>
                  </span>
                </div>
              )}
            </div>
          </div>
          <div>
            <p className='content'>
             <span className='vv'>@{items.replyingTo} </span>{items.content}
            </p>
          </div>
        </div>
      </div>
      {console.log(index)}
      {replyStates[index] &&(
        
        <Comment id={items.id} reply='REPLY' text={`@${items.user?.username}`} />
        
      )}
    </div>
));
  const toggleReply = (index) => {
    const newReplyStates = [...replyStates];
    newReplyStates[index] = !newReplyStates[index];
    return setReplyStates(newReplyStates);
  };
  

  return (
    <div className='comments1'>
      {item}
      <Modal show={show} onHide={() => setShow(false)} handleClose={handleClose} />
    </div>
  );
}

export default Replies;
