import datas from '../Data/data.json';
import { useState } from 'react';
import { UseLocalStorage } from './UseLocalStorage';
import Modals from './Modals';
import deletes from './images/icon-delete.svg';
import plus from './images/icon-plus.svg';
import minus from './images/icon-minus.svg';
import edit from './images/icon-edit.svg';

function Comment(prop) {
  const [value, setValue] = useState(prop.text);
  const [data, setData] = useState(datas.comments);
  const [score, setScore] = useState(0);
  const [edit, setEdit]=useState(false)
    function handleedit(){
        setEdit(!edit)
    }
  function handleClose() {
    deleteItem(prop.id);
    setRep(false);
    return setShow(false);
  }

  const handleShow = () => setShow(true);
  const [show, setShow] = useState(false);
  const [isEditable, setIsEditable] = useState(false);
  const { setItem, getItem, deleteItem } = UseLocalStorage(prop.id);

  const handleInputChange = (e) => {
    setValue(e.target.value);
  };

  const handleEditToggle = () => {
    setIsEditable(!isEditable);
  };

  const [rep, setRep] = useState(false);

  function handleclick() {
    setItem(value);
    setRep(!rep);
  }

  return (
    <form>
      {rep ? (
        <>
        <div className='del'>
          <div className='arithmetic'>
            <img
              className='icon'
              src={plus}
              alt='plus'
              onClick={() => setScore((prevScore) => prevScore + 1)}
            />
            <br /> <br />
            <h4>
              <span className='blue'>{score}</span>
            </h4>

            <img
              className='icon'
              src={minus}
              alt='minus'
              onClick={() => setScore((prevScore) => prevScore - 1)}
            />
          </div>
          <div >
            <div className='del1'>
              <div className='profile'>
                <img src={require(`.${datas.currentUser.image.png}`)} className='profile1' alt='pfp' />
                <h5>{prop.user}</h5>
              </div>

              {rep ? (
                <div className='del2'>
                  <div>
                    <span className='in' onClick={handleShow}>
                      <img className='icon delete' src={deletes} alt='reply' />
                      <h5>
                        <span className='blue'>delete</span>
                      </h5>
                    </span>
                  </div>
                  
                </div>
              ) : (
                ""
              )}
            </div>
            <div>
              <input
                type='text'
                value={getItem(prop.id)}
                className='input'
                id='input2'
                onChange={handleInputChange}
                onBlur={handleEditToggle}
                placeholder='Add a comment...'
                readOnly={edit}
              />
            </div>
            <div className='vi'>
                        <span className='a dd ba' >
                            <img className='icon' src={plus} alt='plus'               onClick={() => setScore((prevScore) => prevScore + 1)}
 />
                            <h4><span className='blue'>{score}</span></h4>
                            <img className='icon' src={minus} alt='minus'               onClick={() => setScore((prevScore) => prevScore + 1)}
 />
                        </span>
                                
                        </div>
          </div>

          <Modals show={show} onHide={() => setShow(false)} handleClose={handleClose} />
          </div>
        </>
      ) : (
        <>
          <div>
            <img className='profile1' src={require(`.${datas.currentUser.image.png}`)} alt='pfp' />
          </div>
          <div>
            {
              <div>
                <input
                  type='text'
                  value={value}
                  className='input'
                  onChange={handleInputChange}
                  onBlur={handleEditToggle}
                  placeholder='Add a comment...'
                />
                <br></br> <br></br>
                <input type='button' onClick={handleclick} value={prop.reply ? prop.reply : 'SEND'} />
              </div>
            }
          </div>
          <Modals show={show} onHide={() => setShow(false)} handleClose={handleClose} />
        </>
      )}
    </form>
  );
}

export default Comment;
