import datas from '../Data/data.json'
import plus from './images/icon-plus.svg'
import minus from './images/icon-minus.svg';
import forward from './images/icon-reply.svg';
import deletes from './images/icon-delete.svg';
import edit from './images/icon-edit.svg'
import { useState } from 'react';
import Replies from './Replies';
import Timestamp from 'react-timestamp';
import Comment from './Comment';
import { gsap } from 'gsap';

function Comments() {
    const [data, setData] = useState(datas.comments);
    const [score, setScore] = useState(data.map((item) => item.score));

    // Initialize an array of reply states, initially set to false for each comment
    const [replyStates, setReplyStates] = useState(data.map(() => false));

    const items = data.map((items, index) => {
        return (
            <div key={items.id}>
                <div className='main'>
                    <div className='arithmetic'>
                        <img
                            className='icon'
                            src={plus}
                            alt='plus'
                            onClick={() => setScore((prevScore) => ({ ...prevScore, [items.id - 1]: prevScore[items.id - 1] + 1 }))}
                        />
                        <br /> <br />
                        <h4><span className='blue'>{score[items.id - 1]}</span></h4>

                        <img
                            className='icon'
                            src={minus}
                            alt='minus'
                            onClick={() => setScore((prevScore) => ({ ...prevScore, [items.id - 1]: prevScore[items.id - 1] - 1 }))}
                        />
                    </div>
                    <div className='inner'>

                        <div className='one'>
                            <div>
                                <span className='in'>
                                    <img className='profile' src={require(`.${items.user.image.png}`)} alt='profile pic' />
                                    <p>{items.user.username}</p>
                                    <p className='time'><Timestamp time={new Date()} format="ago-time" precision={1} /></p>
                                </span>
                            </div>
                            <div>
                                <span className='in ou' id='reply' onClick={() => toggleReply(index)}>
                                    <img className='icon' src={forward} alt='reply' />
                                    <p><span className='blue'>Reply</span></p>
                                </span>
                            </div>
                        </div>
                        <div>
                            <p className='content'>{items.content}</p>
                        </div>
                        <div className='vi'>
                        <span className='a dd'>
                            <img className='icon' src={plus} alt='plus' onClick={() => setScore((prevScore) => ({ ...prevScore, [items.id-1]: prevScore[items.id-1] + 1 }))} />
                            <h4><span className='blue'>{score[items.id-1]}</span></h4>
                            <img className='icon' src={minus} alt='minus' onClick={() => setScore((prevScore) => ({ ...prevScore, [items.id-1]: prevScore[items.id-1] - 1 }))} />
                        </span>
                                <div className='a' id='reply' onClick={() => toggleReply(index)}>
                                    <div>
                                        <img className='' src={forward} alt='reply' />
                                    </div>
                                    <div>
                                        <p><span className='blue'>Reply</span></p>
                                    </div>
                                </div>
                        </div>
                    </div>
                </div>
                <div className={`reply-comment reply-comment-${index}`} id={`reply-comment-${index}`}>
                    {replyStates[index] ? (
                        <Comment reply="REPLY" score={score} length={items.user.username.length} id={items.id} className="reply-comment" user={datas.currentUser.username} text={`@${items.user.username}`} />
                    ) : (
                        ""
                    )}
                </div>
                <Replies item={items.replies} />
            </div>

        )
    })
    const toggleReply = (index) => {
        const newReplyStates = [...replyStates];
        newReplyStates[index] = !newReplyStates[index];
        const comment = document.querySelector(`.reply-comment-${index}`);
        const height = comment.style.height
        setReplyStates(newReplyStates)
        if (newReplyStates[index]) {
            gsap.to(comment, {
                height: 'auto',
                duration: 0.5,
                ease: 'power2.inOut',
                
            });
        } else {
            gsap.to(comment, {
                height: 0,
                duration: 1,
                ease: 'power2.inOut',
                
            });
        }


    };

    return (
        <div className='comments'>
            {items}
            <Comment />
        </div>
    )
}
export default Comments;